button {
  border: 0;
  outline: none;
  border-radius: 0;
  appearance: none;
}

.icon-btn {
  width: 24px;
  height: 24px;
  cursor: pointer;
  transition: 0.2s $ease-in-out-quart;
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: 100% 100%;
  background: $color-808080;

  &_save {
    mask-image: url(/img/save-icon.svg);
  }

  &_remove {
    mask-image: url(/img/remove-icon.svg);
  }
}
