body {
  // position: relative;
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: 116.523%;
  color: $color-808080;
  background: $color-FAFAFA;
}

.layout {
  display: flex;
  flex-wrap: wrap;

  .header {
    width: 100%;
  }

  .sidebar {
    width: 20%;
  }

  &__content {
    width: 80% !important;
    height: calc(100vh - 115px) !important;
  }
}

.sidebar {
  padding: 20px;

  > ul {
    > li {
      > button {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 392.857%;
        background: transparent;
        cursor: pointer;
        color: $color-808080;
      }
    }
  }
}

.node {
  display: flex;
  flex-direction: column;
  position: relative;
  background: $color-FFFFFF;
  border-radius: 6px;
  box-shadow: $shadow-1;
  border: 0;
  color: fade-out($color-1A1919, 0.4);

  &:hover {
    background: fade-out($color-808080, 0.92);
  }

  &.selected {
    background: fade-out($color-808080, 0.8);
  }

  &__title {
    position: relative;
    font-size: 11px;
    font-style: normal;
    font-weight: 500;
    line-height: 154.545%;
    text-transform: uppercase;
    margin: 16px;
    padding-left: 27px;
    font-family: sans-serif;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      width: 24px;
      height: 24px;
      background: url(/img/node-title-icon.svg) no-repeat center 100%/100%;
    }
  }

  .input-title,
  .output-title {
    // padding: 10px;
    color: fade-out($color-1A1919, 0.4);
    font-size: 12px;
  }

  &__remove {
    position: absolute;
    top: 10px;
    right: 10px;
  }

  .socket {
    background: $color-FAFAFA;
    border: 1px solid $color-7EB2FF;
    // width: 9px;
    // height: 9px;
    // margin: 6px 4px;
  }
}

.connection {
  .main-path {
    stroke: $color-D0D0D0;
    stroke-width: 1px;
  }
}
